import { autorun, makeAutoObservable, runInAction } from 'mobx';
import { nanoid } from 'nanoid';
import { getValue, keys, setValue } from './localStorageApi';
import { safeAmounts as safeAmountsFromSettings } from 'src/appSettings';
import { secondsToTime } from 'utils/secondsToTime';
import { makeGameQuestions } from 'src/mocks';
// import { questions as mockQuestions, rating as mockRating } from 'src/mocks';
class AppStore {
    questions = makeGameQuestions();
    questionsFetchingStatus = {
        status: 'idle',
        errorMessage: '',
    };
    currentQuestionNum = 1;
    userAnswer = null;
    startTimeCurrentQuestion = Date.now();
    timerIsPlaying = true;
    timerIsOver = false;
    timerKey = 1;
    safeAmount = 0;
    gameResult = 'none';
    gameHelpers = {
        half: 0,
        audience: 0,
    };
    showFinalScreen = false;
    resultSendingStatus = {
        status: 'idle',
        errorMessage: '',
    };
    timeSpentQuestions = {};
    questionsCountUserAnswered = 0;
    ratingData = [];
    ratingFetchingStatus = {
        status: 'idle',
        errorMessage: '',
    };
    constructor() {
        makeAutoObservable(this, {}, { autoBind: true });
        runInAction(() => {
            this.getRating();
        });
    }
    setQuestions(data) {
        this.questions = data;
    }
    setCurrentQuestionNum(num) {
        this.currentQuestionNum = num;
    }
    nextQuestion() {
        this.currentQuestionNum += 1;
        this.startTimeCurrentQuestion = Date.now();
    }
    setUserAnswer(data) {
        this.userAnswer = data;
    }
    setTimerIsPlaying(isPlaying) {
        this.timerIsPlaying = isPlaying;
    }
    setTimerIsOver(isOver) {
        this.timerIsOver = isOver;
    }
    setTimerKey(key) {
        this.timerKey = key;
    }
    incTimerKey() {
        this.timerKey += 1;
    }
    onPauseTimer() {
        this.timerIsPlaying = false;
    }
    onPlayTimer() {
        this.timerIsPlaying = true;
    }
    onToggleTimer() {
        this.timerIsPlaying = !this.timerIsPlaying;
    }
    setSafeAmount(amount) {
        this.safeAmount = amount;
    }
    setGameResult(result) {
        this.gameResult = result;
    }
    setGameHelpers(helpers) {
        this.gameHelpers = helpers;
    }
    setShowFinalScreen(showFinal) {
        this.showFinalScreen = showFinal;
    }
    onShowFinalScreen() {
        this.showFinalScreen = true;
    }
    onHideFinalScreen() {
        this.showFinalScreen = false;
    }
    setTimeSpentQuestions(data) {
        this.timeSpentQuestions = data;
    }
    onChangeTimeSpentQuestions(time) {
        this.timeSpentQuestions[this.currentQuestionNum] = time;
    }
    setQuestionsCountUserAnswered(count) {
        this.questionsCountUserAnswered = count;
    }
    incQuestionsCountUserAnswered() {
        this.questionsCountUserAnswered += 1;
    }
    onReset(cb) {
        this.questions = makeGameQuestions();
        this.currentQuestionNum = 1;
        this.timerIsPlaying = true;
        this.safeAmount = 0;
        this.gameResult = 'none';
        this.userAnswer = null;
        this.timerIsOver = false;
        this.gameHelpers = {
            half: 0,
            audience: 0,
        };
        this.resultSendingStatus = {
            status: 'idle',
            errorMessage: '',
        };
        this.questionsCountUserAnswered = 0;
        this.timeSpentQuestions = {};
        this.startTimeCurrentQuestion = Date.now();
        cb();
    }
    setRatingData(data) {
        this.ratingData = data;
    }
    resetRatingFetchingStatus() {
        this.ratingFetchingStatus = {
            status: 'idle',
            errorMessage: '',
        };
    }
    get uiRatingData() {
        if (this.ratingData?.length === 0)
            return [];
        return ([...this.ratingData]
            .sort((a, b) => {
            if (a.score === b.score) {
                return a.time - b.time;
            }
            return b.score - a.score;
        })
            // .slice(0, 5)
            .map((item, index) => ({
            ...item,
            time: secondsToTime(item.time),
            orderBy: index + 1,
        })));
    }
    getQuestions() {
        return this.questions;
    }
    sendResult(userName) {
        const totalTimeSpent = Object.values(this.timeSpentQuestions).reduce((timeSpent, acc) => acc + timeSpent, 0);
        const newData = {
            id: nanoid(),
            name: userName,
            score: this.questionsCountUserAnswered,
            time: totalTimeSpent,
        };
        const results = getValue(keys.results);
        const value = results ? [...results, newData] : [newData];
        setValue(keys.results, value);
        this.resultSendingStatus = {
            status: 'loaded',
            errorMessage: '',
        };
    }
    getRating() {
        const data = getValue(keys.results);
        this.setRatingData(data ?? []);
    }
}
export const appStore = new AppStore();
autorun(() => {
    const isLose = appStore.userAnswer?.correct === false;
    if (isLose) {
        setTimeout(() => {
            appStore.setGameResult('lose');
        }, 2000);
    }
});
autorun(() => {
    const isWin = appStore.questions.length === appStore.currentQuestionNum && appStore.userAnswer?.correct;
    const isWinBySafeAmount = appStore.safeAmount >= safeAmountsFromSettings[0];
    setTimeout(() => {
        if (isWin || isWinBySafeAmount) {
            appStore.setGameResult('win');
            setTimeout(() => {
                appStore.onShowFinalScreen();
            }, 1000);
        }
    }, 2000);
});
